/* ==================== Check Box ==================== */
#returns-list input[type=checkbox],
.pay-with-check input[type=checkbox],
.same-address input[type=checkbox],
table.tablesorter input[type=checkbox] {
	visibility: hidden;
}

.pay-with-check,
.returns-check,
.same-address,
.user-check {
	width: 19px;
	height: 19px;
	background: #fcfff4;
	margin: 2px 0;
	position: relative;
}

.pay-with-check label,
.returns-check label,
.same-address label,
.user-check label {
	cursor: pointer;
	position: absolute;
	width: 15px;
	height: 15px;
	left: 2px;
	top: 2px;
	box-shadow: inset 0 1px 1px rgba(0,0,0,0.5), 0 1px 0 rgba(255,255,255,1);
}

.pay-with-check label:after,
.returns-check label:after,
.same-address label:after,
.user-check label:after {
	opacity: 0;
	content: '';
	position: absolute;
	width: 7px;
	height: 4px;
	background: transparent;
	top: 3px;
	left: 3px;
	border: 2px solid #fcfff4;
	border-top: none;
	border-right: none;
	transform: rotate(-45deg);
}

.pay-with-check label:hover::after,
.returns-check label:hover::after,
.same-address label:hover::after,
.user-check label:hover::after {
	opacity: 0.3;
}

.pay-with-check input[type=checkbox]:checked + label:after,
.returns-check input[type=checkbox]:checked + label:after,
.same-address input[type=checkbox]:checked + label:after,
.user-check input[type=checkbox]:checked + label:after {
	opacity: 1;
}







